.footer__link:focus {
  color: #d41711;
  border-color: #d41711;
}
.footer__contact {
  text-align: right;
}
.footer__phone {
  margin-top: 0;
  font-size: 24px;
  line-height: 28px;
  color: #ffffff;
  font-weight: 300;
}
.footer__emphasis {
  font-weight: 700;
}
.footer__work-time {
  font-size: 14px;
  line-height: 18px;
  color: #ffffff;
  font-weight: 300;
}
.footer__email {
  margin-top: 5px;
  margin-bottom: 0;
  font-size: 16px;
  line-height: 18px;
  color: #ffffff;
  font-weight: 300;
  text-decoration: underline;
}
.footer__copy,
.footer__address {
  margin-top: -55px;
  font-size: 16px;
  line-height: 24px;
  font-weight: 300;
  color: #ffffff;
  opacity: 0.4;
}
.footer__address {
  text-align: center;
}
@media (max-width: 1199px) {
  .footer__menu {
    display: none;
  }
  .footer__copy,
  .footer__address {
    margin-top: 10px;
    font-size: 14px;
  }
}
@media (max-width: 767px) {
  .footer__logo-container {
    display: flex;
    justify-content: center;
  }
  .footer__logo {
    width: 145px;
    height: 38px;
    margin: 0 auto;
    margin-bottom: 15px;
  }
  .footer__contact {
    margin-top: 10px;
    text-align: center;
  }
  .footer__copy {
    margin-top: 20px;
    margin-bottom: 5px;
    text-align: center;
  }
}
